
.main {

    min-height: 700px;
    background: url(https://thumbs.dreamstime.com/z/design-white-background-music-notes-design-white-background-music-notes-white-circle-middle-136166733.jpg) no-repeat center center fixed;
    background-size: cover;
}

.top {
    text-align: center;
    font-size: 45px;
    font-family: "Georgia", "Courier New", monospace;
    padding-top: 2%;
}

#backButton {
    width: 100px;
    height: 90px;

margin-left: 45%;
    margin-top: 1%;
border-radius: 122px;
    cursor: pointer;
}
#backButton:hover{
    border: 2px black solid;
}

.allTracks {
    padding: 50px 0;
    margin: auto;
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;


}

.img {
    cursor: pointer;
    width: 100%;
    height: 150px;
}

.imageSmall {

    width: 250px;
    height: 280px;
    font-family: "Georgia", "Courier New", monospace;
    font-size: 16px;
    font-weight: 500;
}

#author {
    padding: 3% 0 0 0;
}

.img {
    border-radius: 25px;
}

.img:hover {
    border: 1px black solid;
    border-radius: 25px;
}

.marian {
    color: burlywood;
    cursor: pointer;
    font-size: 18px;
}

#marianDiv {
    text-align: center;
    padding-bottom: 2%;
}
.divTextArea{
    margin: 20px;
    text-align: center;
}
.textArea{

    width: 150px;
    height: 20px;
    border-radius: 40px;
    padding: 10px;
    cursor: pointer;

}
.textArea:focus{
    outline: none;
    background-color: #f1f2f6;
}
.page{
    min-height: 700px;
    background: url(https://thumbs.dreamstime.com/z/design-white-background-music-notes-design-white-background-music-notes-white-circle-middle-136166733.jpg) no-repeat center center fixed;
    background-size: cover;

}
.pageInfo{

 padding: 50px 170px;
}
.bio{
    text-align: justify;
}
.imgInfo{
text-align: center;
}